
<div class="py-4">
  <vuetable-filter-bar />
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfoTop"
      info-template="Displaying {from} to {to} of {total} pages"
    />
    <vuetable-pagination
      ref="paginationTop"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
  <div class="overflow-x-auto overflow-y-hidden">
    <vuetable
      ref="vuetable"
      :api-url="apiUrl"
      :append-params="moreParams"
      :css="css"
      :fields="fields"
      :per-page="20"
      :sort-order="sortOrder"
      @vuetable:pagination-data="onPaginationData"
      @vuetable:row-clicked="onRowClicked"
      @vuetable:loaded="onLoaded"
    >
      <!-- eslint-disable vue/no-useless-template-attributes -->
      <template
        slot="page-listing-display"
        slot-scope="props"
        :maxValue="maxValue"
        :triBlend="triBlend"
      >
        <!--eslint-enable-->
        <page-result-cell
          :color="triBlend.colorFromPercentage(((props.rowData.pageLoad / maxValue) * 100))"
          :title="props.rowData.title"
          :url="props.rowData.url"
          :width="computeWidth(props.rowData.pageLoad, maxValue)"
        />
      </template>
      <template
        slot="load-time-bar"
        slot-scope="props"
      >
        <request-bar-chart :row-data="props.rowData" />
      </template>
      <template
        slot="data-samples"
        slot-scope="props"
      >
        <sample-size-warning :sample="props.rowData.cnt" />
        {{ props.rowData.cnt }}
      </template>
    </vuetable>
  </div>
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfo"
      info-template="Displaying {from} to {to} of {total} pages"
    />
    <vuetable-pagination
      ref="pagination"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
</div>
